import { useState } from 'react';
import PropTypes from 'prop-types';
import Table from '@mui/material/Table';
import TableBody from '@mui/material/TableBody';
import TableContainer from '@mui/material/TableContainer';
import Paper from '@mui/material/Paper';

import ShareeRow from './ShareRow';
import TableHeader from './TableHeader';

export default function ExistingShares({ shareData }) {
  const [numSelected, setNumSelected] = useState(0);
  const [selected, setSelected] = useState(Array(shareData.sharees.length).fill(0));

  // eslint-disable-next-line no-console
  console.log('share data', shareData, selected);

  const handleCheckBoxClick = (ind) => {
    console.log('handle click index', ind);
    if (selected[ind] === 1) {
      setNumSelected((prevSelected) => prevSelected - 1);
    } else {
      setNumSelected((prevSelected) => prevSelected + 1);
    }

    setSelected([
      ...selected.slice(0, ind),
      1 - selected[ind],
      ...selected.slice(ind + 1),
    ]);
  };

  const handleSelectAllClick = () => {
    if (selected.length > 0 && numSelected < selected.length) {
      setSelected(Array(shareData.sharees.length).fill(1));
      setNumSelected(selected.length);
    } else {
      setSelected(Array(shareData.sharees.length).fill(0));
      setNumSelected(0);
    }
  };

  const handleUnshare = () => {
    const unshareEmails = [];
    for (let i = 0; i < selected.length; i += 1) {
      if (selected[i] === 1) {
        unshareEmails.push(shareData.sharees[i].email);
      }
    }
  };

  return ('sharees' in shareData) ? (
    <TableContainer component={Paper}>
      <Table aria-label="memory share table">
        <TableHeader
          numSelected={numSelected}
          rowCount={shareData.sharees.length}
          onSelectAllClick={handleSelectAllClick}
          handleUnshareClick={handleUnshare}
        />
        <TableBody>
          {shareData.sharees.map((sharee, index) => {
            let name;
            let picture;
            if ('details' in sharee && sharee.details) {
              if ('name' in sharee.details) {
                name = sharee.details.name;
              } else if ('username' in sharee.details && sharee.details.username) {
                name = sharee.details.username;
              } else {
                name = sharee.email;
              }
              if ('picture' in sharee.details) {
                picture = sharee.details.picture;
              } else {
                picture = '';
              }
            } else {
              name = sharee.email;
              picture = '';
            }
            // eslint-disable-next-line no-console
            console.log('name, picture', name, picture);
            return (
              <ShareeRow
                key={sharee.email}
                email={sharee.email}
                name={name}
                picture={picture}
                invitation={sharee.status.invitation}
                accept={sharee.status.accept}
                isSelected={selected[index] === 1}
                onCheckBoxClick={() => handleCheckBoxClick(index)}
              />
            );
          })}
        </TableBody>
      </Table>
    </TableContainer>
  ) : null;
}

ExistingShares.propTypes = {
  shareData: PropTypes.shape({
    sharees: PropTypes.arrayOf(PropTypes.shape({
      email: PropTypes.string.isRequired,
      status: PropTypes.shape({
        accept: PropTypes.string.isRequired,
        invitation: PropTypes.string.isRequired,
      }).isRequired,
      details: PropTypes.shape({
        email: PropTypes.string,
        given_name: PropTypes.string,
        family_name: PropTypes.string,
        name: PropTypes.string,
        picture: PropTypes.string,
        username: PropTypes.string,
        email_verified: PropTypes.string,
      }),
    }).isRequired).isRequired,
  }).isRequired,
};
